'use client';

import { createContext, useContext, useState, useEffect, ReactNode, useMemo, Dispatch, SetStateAction, useCallback } from 'react';
import { ALL_SKILLS, SKILL_CATEGORIES } from '@/lib/skills';
import { useToast } from './use-toast';
import { getCategoryMasteryMessage } from '@/ai/flows/category-mastery-flow';
import { useFirestore, useUser } from '@/firebase';
import { doc, setDoc, getDoc } from 'firebase/firestore';
import { setDocumentNonBlocking } from '@/firebase/non-blocking-updates';

type SkillsState = { [key: string]: boolean };

type SkillContextType = {
  skills: SkillsState;
  setSkills: Dispatch<SetStateAction<SkillsState>>;
  toggleSkill: (skill: string) => void;
  isInitialized: boolean;
  exportSkillsAsMarkdown: () => string;
};

const SkillContext = createContext<SkillContextType | undefined>(undefined);

const SKILLS_STORAGE_KEY = 'web3skills';

const initialSkills = Object.keys(ALL_SKILLS).reduce((acc, skill) => {
    acc[skill] = false;
    return acc;
}, {} as SkillsState);

export function SkillProvider({ children }: { children: ReactNode }) {
  const [skills, setSkills] = useState<SkillsState>({});
  const [isInitialized, setIsInitialized] = useState(false);
  const { toast } = useToast();
  const { user } = useUser();
  const firestore = useFirestore();

  const migrateLocalToFirestore = useCallback(async (userId: string) => {
    try {
      const localSkillsRaw = localStorage.getItem(SKILLS_STORAGE_KEY);
      if (localSkillsRaw) {
        const localSkills = JSON.parse(localSkillsRaw);
        const userSkillsRef = doc(firestore, `users/${userId}/skills`, 'user_skills');
        await setDoc(userSkillsRef, localSkills, { merge: true });
        localStorage.removeItem(SKILLS_STORAGE_KEY);
        setSkills(localSkills);
      }
    } catch (error) {
      console.error("Failed to migrate skills from localStorage to Firestore", error);
    }
  }, [firestore]);


  useEffect(() => {
    const loadSkills = async () => {
      setIsInitialized(false);
      try {
        if (user) {
          const userSkillsRef = doc(firestore, `users/${user.uid}/skills`, 'user_skills');
          const docSnap = await getDoc(userSkillsRef);
          if (docSnap.exists()) {
            const firestoreSkills = docSnap.data();
            const validSkills: SkillsState = {};
            for (const skillName of Object.keys(ALL_SKILLS)) {
              validSkills[skillName] = !!firestoreSkills[skillName];
            }
            setSkills(validSkills);
          } else {
            await migrateLocalToFirestore(user.uid);
            // if still no skills, initialize with empty
            const newDocSnap = await getDoc(userSkillsRef);
            if (!newDocSnap.exists()) {
              setSkills(initialSkills);
            }
          }
        } else {
          const stored = localStorage.getItem(SKILLS_STORAGE_KEY);
          if (stored) {
            const storedSkills = JSON.parse(stored);
            const validSkills: SkillsState = {};
            for (const skillName of Object.keys(ALL_SKILLS)) {
              validSkills[skillName] = !!storedSkills[skillName];
            }
            setSkills(validSkills);
          } else {
            setSkills(initialSkills);
          }
        }
      } catch (error) {
        console.error("Failed to load skills", error);
        setSkills(initialSkills);
      }
      setIsInitialized(true);
    };

    loadSkills();
  }, [user, firestore, migrateLocalToFirestore]);
  

  useEffect(() => {
    if (isInitialized && !user) {
      try {
        localStorage.setItem(SKILLS_STORAGE_KEY, JSON.stringify(skills));
      } catch (error) {
        console.error("Failed to save skills to localStorage", error);
      }
    }
  }, [skills, isInitialized, user]);

  const checkForMastery = useCallback((updatedSkills: SkillsState, originalSkills: SkillsState) => {
    Object.entries(SKILL_CATEGORIES).forEach(async ([categoryName, categorySkills]) => {
      const wasComplete = categorySkills.every(skill => originalSkills[skill]);
      const isNowComplete = categorySkills.every(skill => updatedSkills[skill]);

      if (isNowComplete && !wasComplete) {
        try {
          const { message } = await getCategoryMasteryMessage({ categoryName });
          toast({
            title: `Category Mastered! `,
            description: message,
          });
        } catch (e) {
          console.error("Failed to get mastery message", e);
          toast({
            title: `Category Mastered! `,
            description: `Congratulations on mastering ${categoryName}!`,
          });
        }
      }
    });
  }, [toast]);
  
  const toggleSkill = (skill: string) => {
    const originalSkills = { ...skills };
    const updatedSkills = { ...skills, [skill]: !skills[skill] };
    
    setSkills(updatedSkills);
    checkForMastery(updatedSkills, originalSkills);
    
    if (user) {
      const userSkillsRef = doc(firestore, `users/${user.uid}/skills`, 'user_skills');
      setDocumentNonBlocking(userSkillsRef, updatedSkills, { merge: true });
    }
  };
  
  const exportSkillsAsMarkdown = () => {
    let markdown = '# My Web3 Skills\n\n';
    markdown += `*Generated on ${new Date().toLocaleDateString()}*\n\n`;

    Object.entries(SKILL_CATEGORIES).forEach(([categoryName, categorySkills]) => {
      markdown += `## ${categoryName}\n\n`;
      const completedSkills = categorySkills.filter(skill => skills[skill]);
      if (completedSkills.length > 0) {
        completedSkills.forEach(skill => {
          markdown += `- [x] ${skill}\n`;
        });
      } else {
        markdown += `_No skills selected in this category._\n`;
      }
      markdown += '\n';
    });

    return markdown;
  };

  const value = useMemo(() => ({
    skills,
    setSkills,
    toggleSkill,
    isInitialized,
    exportSkillsAsMarkdown,
  }), [skills, isInitialized, exportSkillsAsMarkdown]);

  return (
    <SkillContext.Provider value={value}>
      {children}
    </SkillContext.Provider>
  );
}

export const useSkills = () => {
  const context = useContext(SkillContext);
  if (context === undefined) {
    throw new Error('useSkills must be used within a SkillProvider');
  }
  return context;
};
