'use server';
/**
 * @fileOverview Flow to generate a congratulatory message for completing a skill category.
 *
 * - getCategoryMasteryMessage - A function that returns a congratulatory message.
 * - CategoryMasteryInput - The input type for the getCategoryMasteryMessage function.
 * - CategoryMasteryOutput - The return type for the getCategoryMasteryMessage function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const CategoryMasteryInputSchema = z.object({
  categoryName: z.string().describe('The name of the skill category the user has mastered.'),
});
export type CategoryMasteryInput = z.infer<typeof CategoryMasteryInputSchema>;

const CategoryMasteryOutputSchema = z.object({
  message: z.string().describe('A short, encouraging, and congratulatory message for the user.'),
});
export type CategoryMasteryOutput = z.infer<typeof CategoryMasteryOutputSchema>;


export async function getCategoryMasteryMessage(input: CategoryMasteryInput): Promise<CategoryMasteryOutput> {
    return categoryMasteryFlow(input);
}


const prompt = ai.definePrompt({
    name: 'categoryMasteryPrompt',
    input: { schema: CategoryMasteryInputSchema },
    output: { schema: CategoryMasteryOutputSchema },
    prompt: `You are a motivating career coach in the web3 space. A user has just mastered all the skills in a specific category.

Generate a short (1-2 sentence), fun, and encouraging message congratulating them for mastering the "{{categoryName}}" category. Use a positive and slightly celebratory tone. Mention the category name.`,
});

const categoryMasteryFlow = ai.defineFlow(
    {
        name: 'categoryMasteryFlow',
        inputSchema: CategoryMasteryInputSchema,
        outputSchema: CategoryMasteryOutputSchema,
    },
    async (input) => {
        const { output } = await prompt(input);
        return output!;
    }
);
